import dnIcon from '../../components/dn-icon/dn-icon.vue';
export default {
	components: {
		dnIcon
	},
	data() {
		return {
			//点赞创建时间
			likeStatus: [],
			//点赞的数量
			likes: [],
			//是否已经点赞 true or false
			color: [],
			userDto: {},
			date: new Date(),
			// colors: ['indigo', 'warning', 'pink darken-2', 'red lighten-1', 'deep-purple accent-4'],
			listData: [],
			banner: {}
			// bannercom: {}
		};
	},
	methods: {
		//点赞换色
		changeColor(index) {
			//已经点赞
			if (this.color[index]) {
				//改变数组状态为false
				this.color.splice(index, 1, false);
				//点赞数减一
				this.likes.splice(index, 1, this.likes[index] - 1);
				console.log(this.color)
			} else {
				//改变数组状态为true
				this.color.splice(index, 1, true);
				//点赞数减一
				this.likes.splice(index, 1, this.likes[index] + 1);
				console.log(this.color)
			}
		},
		// 点赞的方法
		thumbUp(journalId) {
			// alert(journalId)
			// this.likeStatus[index] = this.formatDateTime();
			// this.changeColor(index);
			// console.log(this.likeStatus[index]);
			uni.request({
				url: this.$baseUrl + '/journal/user/journal/addlike',
				method: 'PUT',
				data: {
					journalId: journalId,
					userId: this.userDto.id
				}
			});
		},
		// 取消点赞的方法
		thumDown(journalId) {
			// alert(journalId)
			// this.likeStatus[index] = null;
			// this.changeColor(index);
			// console.log(this.likeStatus[index]);
			uni.request({
				url: this.$baseUrl + '/journal/user/journal/cancellike',
				method: 'DELETE',
				data: {
					journalId: journalId,
					userId: this.userDto.id
				}
			});
		},
		changeStatus() {
			//获取日志列表之前完成更新点赞操作
			console.log(this.color)
			for (var i = 0; i < this.color.length; i++) {
				if (this.color[i]) {
					this.thumbUp(this.listData[i].id);
				} else {
					this.thumDown(this.listData[i].id);
				}
			}
		},

		tapPopup(e) {
			uni.showToast({
				title: e.title,
				icon: 'none'
			});
		},
		taptext(e, index) {
			this.x = e.touches[0].clientX;
			this.y = e.touches[0].clientY;

			this[`value${index}`] = !this[`value${index}`];
		},

		getList() {
			// var _self = this;
			uni.request({
				url: this.$baseUrl + '/journal/index/data',
				method: 'POST',
				data: {
					id: this.userDto.id
				},
				success: res => {
					console.log(this.userDto.id);
					//请求成功后将日志列表数据赋值给listData
					console.log(res.data.data);
					this.listData = res.data.data;
					console.log(this.listData);
					for (var i = 0; i < this.listData.length; i++) {
						this.likeStatus[i] = this.listData[i].likeStatus;
						if (this.listData[i].likeStatus != null) {
							this.color[i] = true;
						} else {
							this.color[i] = false;
						}
						this.likes[i] = this.listData[i].likes;
					}
					console.log(this.color);
				}
			});
		},
		goDetail(item) {
			let detail = {
				id: item.id,
				userId: item.userId,
				thumbnail: item.thumbnail,
				createTime: item.createTime,
				comments: item.comments,
				title: item.title,
				avatar: item.avatar,
				nickname: item.nickname,
				likes: item.likes,
				content: item.content
			};
			uni.navigateTo({
				url: '../j_detail/j_detail?detailDate=' + encodeURIComponent(JSON.stringify(detail))
			});
		}
	},

	onLoad() {
		let that = this;
		uni.getStorage({
			key: 'user',
			success: function(res) {
				that.userDto = res.data;
				console.log(that.userDto);
				that.getList();
			}
		});
		setInterval(function() {
			that.changeStatus();
		}, 3600000);
	},

	onHide() {
		console.log('界面隐藏');
		this.changeStatus();
		this.getList();
	}
};
